'use client';

import { useState, useEffect } from 'react';
import {
    LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend,
    BarChart, Bar, PieChart, Pie, Cell
} from 'recharts';
import { AnalyticsStats } from '@/types';

const COLORS = ['#00C853', '#00E676', '#69F0AE', '#B9F6CA', '#E0E0E0'];

export default function AnalyticsPage() {
    const [stats, setStats] = useState<AnalyticsStats | null>(null);
    const [period, setPeriod] = useState<'7d' | '30d' | '90d'>('7d');
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        const fetchStats = async () => {
            setIsLoading(true);
            try {
                const res = await fetch(`/api/analytics/stats?hub_id=1&period=${period}`);
                const data = await res.json();
                if (data.success) {
                    setStats(data.data);
                }
            } catch (error) {
                console.error('Failed to fetch analytics:', error);
            } finally {
                setIsLoading(false);
            }
        };
        fetchStats();
    }, [period]);

    // Mock data for visualization when no real data
    const chartData = stats?.viewsByDay?.length ? stats.viewsByDay : [
        { date: 'Mon', views: 45, clicks: 20 },
        { date: 'Tue', views: 65, clicks: 35 },
        { date: 'Wed', views: 85, clicks: 45 },
        { date: 'Thu', views: 120, clicks: 80 },
        { date: 'Fri', views: 95, clicks: 60 },
        { date: 'Sat', views: 150, clicks: 90 },
        { date: 'Sun', views: 180, clicks: 110 },
    ];

    const deviceData = stats?.viewsByDevice?.length ? stats.viewsByDevice : [
        { device: 'Mobile', count: 625 },
        { device: 'Desktop', count: 512 },
        { device: 'Tablet', count: 113 },
    ];

    const countryData = stats?.viewsByCountry?.length ? stats.viewsByCountry : [
        { country: 'IN', count: 456 },
        { country: 'US', count: 298 },
        { country: 'UK', count: 156 },
        { country: 'CA', count: 89 },
        { country: 'DE', count: 67 },
    ];

    const ctr = stats && stats.totalViews > 0
        ? ((stats.totalClicks / stats.totalViews) * 100).toFixed(1)
        : '0.0';

    return (
        <div className="space-y-6">
            {/* Header */}
            <div className="flex items-center justify-between">
                <h1 className="text-2xl font-bold text-white">Analytics</h1>
                <div className="flex bg-[#111] border border-[#222] rounded-lg p-1">
                    {(['7d', '30d', '90d'] as const).map((p) => (
                        <button
                            key={p}
                            onClick={() => setPeriod(p)}
                            className={`px-4 py-2 text-sm rounded-md transition-colors ${period === p
                                    ? 'bg-[#00C853] text-black font-bold'
                                    : 'text-[#9A9A9A] hover:text-white'
                                }`}
                        >
                            {p === '7d' ? 'Week' : p === '30d' ? 'Month' : 'Quarter'}
                        </button>
                    ))}
                </div>
            </div>

            {/* KPIs */}
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                <KpiCard
                    label="Total Views"
                    value={stats?.totalViews || 0}
                    icon="ðŸ‘ï¸"
                />
                <KpiCard
                    label="Total Clicks"
                    value={stats?.totalClicks || 0}
                    icon="ðŸ–±ï¸"
                />
                <KpiCard
                    label="CTR"
                    value={`${ctr}%`}
                    icon="ðŸ“Š"
                />
                <KpiCard
                    label="Unique Visitors"
                    value={stats?.uniqueVisitors || 0}
                    icon="ðŸ‘¥"
                />
            </div>

            {/* Traffic Chart */}
            <div className="bg-[#111] rounded-xl border border-[#222] p-6">
                <h2 className="text-lg font-bold text-white mb-4">Traffic Overview</h2>
                <div className="h-64">
                    <ResponsiveContainer width="100%" height="100%">
                        <LineChart data={chartData}>
                            <CartesianGrid strokeDasharray="3 3" stroke="#222" vertical={false} />
                            <XAxis dataKey="date" stroke="#666" fontSize={12} tickLine={false} />
                            <YAxis stroke="#666" fontSize={12} tickLine={false} axisLine={false} />
                            <Tooltip
                                contentStyle={{ backgroundColor: '#0a0a0a', border: '1px solid #333', borderRadius: '8px' }}
                                labelStyle={{ color: '#9A9A9A' }}
                            />
                            <Legend />
                            <Line type="monotone" dataKey="views" name="Views" stroke="#00C853" strokeWidth={2.5} dot={{ r: 3 }} />
                            <Line type="monotone" dataKey="clicks" name="Clicks" stroke="#666" strokeWidth={2} strokeDasharray="5 5" />
                        </LineChart>
                    </ResponsiveContainer>
                </div>
            </div>

            {/* Two Column Charts */}
            <div className="grid md:grid-cols-2 gap-6">
                {/* Device Breakdown */}
                <div className="bg-[#111] rounded-xl border border-[#222] p-6">
                    <h2 className="text-lg font-bold text-white mb-4">Device Breakdown</h2>
                    <div className="h-48">
                        <ResponsiveContainer width="100%" height="100%">
                            <PieChart>
                                <Pie
                                    data={deviceData}
                                    dataKey="count"
                                    nameKey="device"
                                    cx="50%"
                                    cy="50%"
                                    outerRadius={70}
                                    label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                                    labelLine={false}
                                >
                                    {deviceData.map((_, index) => (
                                        <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                                    ))}
                                </Pie>
                                <Tooltip />
                            </PieChart>
                        </ResponsiveContainer>
                    </div>
                </div>

                {/* Country Distribution */}
                <div className="bg-[#111] rounded-xl border border-[#222] p-6">
                    <h2 className="text-lg font-bold text-white mb-4">Top Countries</h2>
                    <div className="h-48">
                        <ResponsiveContainer width="100%" height="100%">
                            <BarChart data={countryData} layout="vertical">
                                <CartesianGrid strokeDasharray="3 3" stroke="#222" horizontal={false} />
                                <XAxis type="number" stroke="#666" fontSize={12} />
                                <YAxis dataKey="country" type="category" stroke="#666" fontSize={12} width={40} />
                                <Tooltip
                                    contentStyle={{ backgroundColor: '#0a0a0a', border: '1px solid #333', borderRadius: '8px' }}
                                />
                                <Bar dataKey="count" fill="#00C853" radius={[0, 4, 4, 0]} />
                            </BarChart>
                        </ResponsiveContainer>
                    </div>
                </div>
            </div>

            {/* Top Links Table */}
            <div className="bg-[#111] rounded-xl border border-[#222] p-6">
                <h2 className="text-lg font-bold text-white mb-4">Top Performing Links</h2>
                <div className="overflow-x-auto">
                    <table className="w-full">
                        <thead>
                            <tr className="border-b border-[#222] text-[#9A9A9A] text-sm">
                                <th className="text-left py-3 font-medium">Link</th>
                                <th className="text-right py-3 font-medium">Clicks</th>
                                <th className="text-right py-3 font-medium">CTR</th>
                            </tr>
                        </thead>
                        <tbody>
                            {(stats?.topLinks?.length ? stats.topLinks : [
                                { id: 1, title: 'My Portfolio', clicks: 452, ctr: 12 },
                                { id: 2, title: 'GitHub', clicks: 320, ctr: 8 },
                                { id: 3, title: 'LinkedIn', clicks: 280, ctr: 7 },
                            ]).map((link) => (
                                <tr key={link.id} className="border-b border-[#222] hover:bg-[#1a1a1a]">
                                    <td className="py-3 text-white truncate max-w-[200px]">{link.title}</td>
                                    <td className="py-3 text-right text-[#00C853] font-medium">{link.clicks}</td>
                                    <td className="py-3 text-right text-[#9A9A9A]">{link.ctr}%</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}

function KpiCard({ label, value, icon }: { label: string; value: string | number; icon: string }) {
    return (
        <div className="bg-[#111] rounded-xl border border-[#222] p-4">
            <div className="flex items-center gap-2 mb-1">
                <span className="text-lg">{icon}</span>
                <p className="text-[#666] text-xs uppercase tracking-wider">{label}</p>
            </div>
            <p className="text-2xl font-bold text-white">
                {typeof value === 'number' ? value.toLocaleString() : value}
            </p>
        </div>
    );
}
